import React from 'react'
import Airtable from 'airtable'
const fetch = require('node-fetch')

// sneaky airtable api key in hidden environment variable
let API_KEY = process.env.REACT_APP_AIRTABLE_API_KEY
Airtable.configure({apiKey: API_KEY, endpointUrl: 'https://api.airtable.com'})

class TestDescriptions extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      desc: '',
      target: this.props.target,
      descriptions: this.props.descriptions,
      source: 'en',
      i: 0
    }

    // this.translate = this.translate.bind(this)
  }

  printDescription() {
    let descs = this.state.descriptions
    let i = this.state.i

    this.setState({
      desc: descs[i]
    }, () => {
      return
    })
  }

  printTranslation(desc) {
    let fromLang = 'en'
    let toLang = this.state.target
    let text = desc

    let API_KEY = process.env.REACT_APP_GOOGLE_API_KEY

    let url = `https://translation.googleapis.com/language/translate/v2?key=${API_KEY}`;
    url += '&q=' + encodeURI(text);
    url += `&source=${fromLang}`;
    url += `&target=${toLang}`;

    fetch(url, {
      method: 'GET',
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      }
    })
    .then(res => res.json())
    .then((response) => {
      let translation = response.data.translations[0].translatedText
      this.setState({
        translation: translation
      }, () => {
        return
      })
    })
    .catch(error => {
      console.log("There was an error with the translation request: ", error);
    });
  }

  btnClick(rating) {
    console.log(rating);
    console.log(this.state.target);
    console.log(this.state.descriptions[this.state.i]);
    console.log(this.state.translation);
    let base = new Airtable({apiKey: API_KEY}).base('appi0N2QTGTlL5MEY');

    base('ratings').create({
      "rating": rating,
      "language": this.state.target,
      "description": this.state.descriptions[this.state.i],
      "translation": this.state.translation
    }, function(err, record) {
        if (err) { console.error(err); return; }
    });


    this.setState(prevState => ({
      i: this.state.i + 1,
      translation: ''
    }),() => {
      this.printTranslation(this.state.descriptions[this.state.i])
    })
  }

  componentWillMount() {
    let desc = this.state.descriptions[this.state.i]
    this.printTranslation(desc)
  }

  render() {
    let desc = this.state.descriptions[this.state.i]

    if (this.state.i < 10) {
      return (
        <div className='testDescriptions'>

          <section className='descriptionEn'>
            <h3>Before</h3>
            <span>{ desc }</span>
          </section>

          <section className="descriptionTr">
            <h3>After</h3>
            <span>{ this.state.translation }</span>
          </section>

          <button className="good" onClick={ (e) => {
              e.preventDefault()
              this.btnClick('good')
            }
          }>Very helpful! <span className="emoji" role="img" aria-label="big-happy">üòÅ</span></button>

          <button className="okay" onClick={ (e) => {
              e.preventDefault()
              this.btnClick('okay')
            }
          }>Helpful <span className="emoji" role="img" aria-label="smile">üôÇ</span></button>

          <button className="bad" onClick={ (e) => {
              e.preventDefault()
              this.btnClick('bad')
            }
          }>Not helpful <span className="emoji" role="img" aria-label="sad">üòñ</span></button>

        </div>
      )
    } else {
      document.querySelector('h2').remove()

      return (
        <div className="thankyou">
            <h3>Thanks for all your help</h3>
            <img src="getFat.gif" alt="get fat"/>
            <a href="https://yummy-translations.herokuapp.com/">
              play again
            </a>
        </div>
      )
    }
  }
}

export default TestDescriptions
