import React, { Component } from 'react';
import './App.css';
import SelectLanguage from './SelectLanguage.js'
import TestDescriptions from './TestDescriptions.js'
import Airtable from 'airtable'


// sneaky airtable api key in hidden environment variable
let API_KEY = process.env.REACT_APP_AIRTABLE_API_KEY
Airtable.configure({apiKey: API_KEY, endpointUrl: 'https://api.airtable.com'})

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      numberOfDesc: 10,
      descriptions: [],
      source: 'en',
      target: '',
      ready: false,
      languages: [
        {
          language: 'üá´üá∑ French',
          code: 'fr'
        },
        {
          language: 'üá®üá≥ Mandarin',
          code: 'ma'
        },
        {
          language: 'üá¨üá∑ Greek',
          code: 'el'
        },
        {
          language: 'üáØüáµ Japanese',
          code: 'ja'
        },
        {
          language: 'üáÆüá≥ Hindi',
          code: 'hi'
        },
        {
          language: 'üá®üá≥ Cantonese',
          code: 'cn'
        },
        {
          language: 'üá∞üá∑ Korean',
          code: 'ko'
        },
        {
          language: 'üá©üá™ German',
          code: 'de'
        },
        {
          language: 'üáÆüá© Indonesian',
          code: 'id'
        },
        {
          language: 'üá®üá≥ Chinese - Simplified',
          code: 'zh-CN'
        },
        {
          language: 'üá®üá≥ Chinese - Traditional',
          code: 'zh-TW'
        },
        {
          language: 'üá™üá∏ Spanish',
          code: 'es'
        },
        {
          language: 'üá∑üá∫ Russian',
          code: 'ru'
        }
      ]
    }

    this.setLanguage = this.setLanguage.bind(this)
    this.selectRandomDescriptions = this.selectRandomDescriptions.bind(this)
  }

  componentWillMount() {
    this.selectRandomDescriptions()
  }

  setLanguage(code) {
    this.setState({
      target: code
    }, () => {
      // console.log(this.state.target)
    })
  }

  printLanguage(code) {
    let index = this.state.languages.findIndex(l => l.code === code)
    return this.state.languages[index].language
  }

  selectRandomDescriptions() {
    let self = this
    let descriptions = []
    let base = Airtable.base('apptSqpNwahy0x0YK');
    base('Database').select({
      view: "Grid view",
      // filterByFormula: `NOT(Definition = BLANK()) = 1`
    }).eachPage(function page(records, fetchNextPage) {

      records.forEach(function(record) {
        descriptions.push(record.get('Item Description'))
      });

      fetchNextPage();

    }, function done(err) {
      if (err) {
        console.error(err);
        return;
      }
      self.setState({
        ready: true
      }, () => { console.log('ready? ' + self.state.ready)})
      self.selectRandom(descriptions, self.state.numberOfDesc)
    })
  }

  selectRandom(descs, num) {
    let randoms = []
    let i = 0
    while (i < num) {
      let rand = descs[Math.floor(Math.random() * descs.length)]
      if (rand !== undefined && randoms.indexOf(rand) === -1) {
        randoms.push(rand)
        i++
      }
    }
    this.setState({
      descriptions: randoms
    }, () => {
      // console.log(this.state.descriptions)
    })
  }

  render() {
    let noLanguage = true
    document.querySelector('body').classList.add('purple')
    if (this.state.target !== '') {
      noLanguage = false
      document.querySelector('body').classList.remove('purple')
      document.querySelector('body').classList.add('purpink')
    }
    let ready = this.state.ready

    return (
      <div className="App">

        <header>
          <h1>Yummy translations</h1>
        </header>

        { noLanguage ?
          <section>
            { ready ?
              <div>
                <span className="selectHead">Select your language</span>
                <SelectLanguage
                  target={ this.state.target }
                  languages={ this.state.languages }
                  onSetLang={ this.setLanguage }
                />
              </div>
              :
              <div className="helpingHand">
                <h3>Thanks for giving us a HELPING HAND</h3>
                <img src="helpinghand.gif" alt="helping hand"/>
              </div>
            }
          </section>
          :
          <section>
            <h2>{ this.printLanguage(this.state.target) }</h2>
            <TestDescriptions
              target={ this.state.target }
              descriptions={ this.state.descriptions }
             />
          </section>
        }

      </div>
    );
  }
}

export default App;
