import React from 'react'

class SelectLanguage extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      target: '',
      languages: this.props.languages,
    }
  }

  listLanguages(list) {
    let languages = list
    let radios = []

    languages.forEach((l) => {
      radios.push(
        <div key={ radios.length }>

          <label className="language" htmlFor={ l.code }>

            <input
              type="radio"
              name="language"
              id={ l.code }
              value={ l.code }
              onClick={ (e)=>{this.setLanguage(e.target.value)} } />

              { l.language }
              <span className="languageRadio" />

          </label>

        </div>
      )
    })
    return radios
  }

  setLanguage(code) {
    this.setState({
      target: code
    }, () => {
      // console.log(this.state.target)
    })
  }

  render() {
    return (
      <div className='selectLanguage'>
        { this.listLanguages(this.state.languages) }

        <button onClick={ (e) => {
            e.preventDefault()
            this.props.onSetLang(this.state.target)
          }
         }>Next step!</button>
      </div>
    )
  }
}

export default SelectLanguage
